import mixinDraw from '../../../mixins/tool/mixinDraw'
import { makeMaterial } from '../../../utils/util'

export default {
  name: 'vc-handler-draw-polyline',
  mixins: [mixinDraw],
  data () {
    return {
      drawType: 'polylineDrawing',
      drawing: false,
      polylines: []
    }
  },
  props: {
    depthTest: {
      type: Boolean,
      default: false
    },
    polylineMaterial: {
      type: Object,
      default: () => {
        return {
          fabric: {
            type: 'Color',
            uniforms: {
              color: '#51ff00'
            }
          }
        }
      }
    },
    polylineWidth: {
      type: Number,
      default: 2
    }
  },
  methods: {
    getPolylineMaterial (val) {
      if (!(val instanceof Cesium.Material)) {
        return makeMaterial(val)
      } else {
        return val
      }
    }
  }
}
